var ttt = 0;
//高德SDK
import amap from '@/common/SDK/amap-wx.js';
export default {
	data() {
		return {
			showHeader:true,
			afterHeaderOpacity: 1,//不透明度
			headerPosition: 'fixed',
			headerTop:null,
			statusTop:null,
			nVueTitle:null,
			city: '石家庄',
			currentSwiper: 0,
			// 轮播图片
			swiperList: [
				{ id: 1, src: 'url1', img: '/static/img/c1.jpg' },
				{ id: 2, src: 'url2', img: '/static/img/c2.jpg' },
				{ id: 3, src: 'url3', img: '/static/img/c3.jpg' }
			],
			// 分类菜单
			categoryList: [
				{ id: 1, name: '北京', img: '/static/img/category/city.png' },
				{ id: 2, name: '上海', img: '/static/img/category/city.png' },
				{ id: 3, name: '广州', img: '/static/img/category/city.png' },
				{ id: 4, name: '深圳', img: '/static/img/category/city.png' },
				{ id: 5, name: '成都', img: '/static/img/category/city.png' },
				{ id: 6, name: '重庆', img: '/static/img/category/city.png' },
				{ id: 7, name: '武汉', img: '/static/img/category/city.png' },
				{ id: 8, name: '杭州', img: '/static/img/category/city.png' }
			],
			Promotion: [],
			//猜你喜欢列表
			likeList:{},
			likeListOffice:{},
			loadingText: '正在加载...'
		};
	},
	onPageScroll(e) {
		//兼容iOS端下拉时顶部漂移
		this.headerPosition = e.scrollTop>=0?"fixed":"absolute";
		this.headerTop = e.scrollTop>=0?null:0;
		this.statusTop = e.scrollTop>=0?null:-this.statusHeight+'px';
	},
	//下拉刷新，需要自己在page.json文件中配置开启页面下拉刷新 "enablePullDownRefresh": true
	onPullDownRefresh() {
		setTimeout(function() {
			uni.stopPullDownRefresh();
		}, 1000);
	},
	//上拉加载，需要自己在page.json文件中配置"onReachBottomDistance"
	onReachBottom() {
		
	},
	onLoad() {
		uni.request({
		    url: 'http://zy.eatclub.wang:3000/list/sr', //仅为示例，并非真实接口地址。
		    method: 'GET',  
		    data: {},  
		    success: res => {  
				this.likeList = res.data.data;
		    },  
		    fail: () => {},  
		    complete: () => {}
		});
		
		uni.request({
		    url: 'http://zy.eatclub.wang:3000/list/office', //仅为示例，并非真实接口地址。
		    method: 'GET',  
		    data: {},  
		    success: res => {  
				this.likeListOffice = res.data.data;
		    },  
		    fail: () => {},  
		    complete: () => {}
		});
		











		this.amapPlugin = new amap.AMapWX({
			//高德地图KEY，随时失效，请务必替换为自己的KEY，参考：http://ask.dcloud.net.cn/article/35070
			key: '3ef137506182cf7ca6cd3e2cee9daf31'
		});
		// 定位地址
		this.amapPlugin.getRegeo({
			success: data => {
				this.city = data[0].regeocodeData.addressComponent.city.replace(/市/g, ''); //把"市"去掉
				console.log(this.city);



			}
		});
	},
	methods: {
		
		//消息列表
		toMsg(){
			uni.navigateTo({
				url:'../../msg/msg'
			})
		},
		//搜索跳转
		toSearch() {
			// uni.showToast({ title: '建议跳转到新页面做搜索功能' });
		},
		//轮播图跳转
		toSwiper(e) {
			uni.showToast({ title: e.src, icon: 'none' });
		},
		//分类跳转
		toCategory(e) {
			//uni.showToast({title: e.name,icon:"none"});
			uni.setStorageSync('catName',e.name);
			uni.navigateTo({
				url: '../../goods/goods-list/goods-list?cid='+e.id+'&name='+e.name
			});
		},
		//推荐商品跳转
		toPromotion(e) {
			uni.showToast({ title: e.title, icon: 'none' });
		},
		//自习室办公室跳转
		toStudyroom(e) {
			uni.navigateTo({
				url: '../../goods/goods?srid=' + e
			});
		},
		toOffice(e) {
			// uni.showToast({ title: '商品' + e.goods_id, icon: 'none' });
			uni.navigateTo({
				url: '../../goods/goods?officeid=' + e
			});
		},
		//轮播图指示器
		swiperChange(event) {
			this.currentSwiper = event.detail.current;
		},
		// 城市选择
		selectCity(e) {
			this.city = e;
			uni.setStorage({
			    key: 'city',
			    data: e,
			    success: function () {
			        console.log('success');
			    }
			});
		}
	}
};
